import { Component, h, Prop, Element, State, Listen, Watch } from '@stencil/core';

@Component({
  tag: 'ks-radio-tile',
  styleUrl: 'radioTile.css'
})
export class RadioTile {
    @Element() root: HTMLElement;

    @Prop() name: string;
    @Prop() value: string;

    @Prop() color: string;
    @Prop() logo: string;
    @Prop() price: string;
    @Prop() service: string;
    @Prop() time: string;

    @Prop({mutable: true, reflect: true}) active: boolean;

    CheckHandler() {
        const inputs = document.querySelectorAll(`input[name=${this.name}]`) as NodeListOf<HTMLInputElement>;
        const thisInput = this.root.querySelector("input");

        inputs.forEach((input)=>{

            if(input == thisInput)
                this.active = thisInput.checked;
            else {
                const parent = input.parentElement.parentElement as HTMLKsRadioTileElement;
                parent.active = input.checked
            }
        })
    }

    componentDidLoad() {
        this.root.style.display = "block";
        this.root.style.width = "100%";

        this.resizeHandler();
        this.MobileWatcher(this.mobile);
    }

    @State() mobile: boolean = false;

    @Listen('resize', { target: 'window' })
    resizeHandler() {
        if (window.innerWidth  <= 620)
            this.mobile = true;
        else this.mobile = false;
    }

    @Watch('mobile')
    MobileWatcher(mobile: boolean) {
        this.root.style.maxWidth = mobile ? "100%" : "260px";
    }

    render() {
        const mutedColor = "#eeeeee";

        const titleStyle = {
            backgroundColor: this.active ? this.color : "#a7a7a7",
            transition: "background-color 0.3s ease",
            color: "white", 
            height: "50px"
        }

        const priceStyle = {
            fontSize: "20px",
            padding: "5px 10px"
        }

        const infoStyle = {
            backgroundColor: mutedColor,
            padding: "8px", 
            fontSize: "16px",
            borderTop: "1px solid rgba(0, 0, 0, 0.1)",
            transition: "background-color 0.1s ease-in, color 0.1s ease-in"
        }

        return(
            <label>
                <input hidden type="radio" name={this.name} value={this.value} onChange={()=>this.CheckHandler()}/>

                <div>
                    {this.price ? 
                        <div class={"uk-flex uk-flex-middle uk-flex-between" + (this.active ? "" : " hover")} style={titleStyle}>
                            <div class="uk-flex-auto">
                                <ks-image src={this.logo} alt={this.service} width="130" height="40"></ks-image>
                            </div>

                            <div class="ks-text-decorated uk-text-right" style={priceStyle}>
                                {this.price}
                            </div>
                        </div>
                    :
                        <div class="uk-flex uk-flex-middle uk-flex-center" style={titleStyle}>
                            <ks-image src={this.logo} alt={this.service} width="140" height="40"></ks-image>
                        </div>
                    }
                    
                    <div class={`uk-flex uk-flex-middle ${this.time ? "uk-flex-between" : "uk-flex-center"}`} style={infoStyle} hidden={this.mobile && !this.active}>
                        <div>
                            {this.service}
                        </div>
                        <div class="uk-text-right">
                            {this.time}
                        </div>
                    </div>
                </div>
            </label>
        );
    }
}